// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.header {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  align-items: center;
  padding-top: 20px;
  margin-bottom: 16px;
  width: 1230px;

  &__logo {
    width: 70px;
    height: 28px;
    margin-right: 110px;
  }

  &__number {
    display: flex;
    width: 60px;
    height: 60px;
    justify-content: center;
    align-items: center;
    margin-right: 40px;
    background-color: #e3e3e4;
    border-radius: 50%;
    font-family: 'Pragmatica Bold';
    font-size: 24px;
    line-height: 28px
  }

  &__items {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-right: 30px;
  }

  &__item {
    display: flex;
    margin-right: 45px;
    align-items: center;

    &:nth-child(3) {
      margin-right: 55px;
    }

    &:nth-child(4) {
      margin-right: 64px;
    }
  }

  &__item-icon {
    //
  }

  &__item-link {
    display: inline-block;
    font-family: 'Pragmatica light';
    text-decoration: none;
    font-size: 14px;
    line-height: 24.5px;
    color: #000000;
    margin-left: 20px;
  }

  &__item--search {
    width: 100px;
    margin-right: 0;
    margin-left: 0;

    & input {
      border: none;
      width: 50px;
    }
  }

  &__item-link--search {
    line-height: 17px;
    color: #818285;
  }
}
