// В этом файле должны быть стили для БЭМ-блока slider-news, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.slider-news {

  $block-name:         &; // #{$block-name}__element

    display: flex;
    flex-direction: column;
    width: 880px;
    min-height: 436px;
    padding-top: 45px;

  &__item {
    //
  }

  &__video {
    display: flex;
    padding-left: 315px;
  }

  &__video-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    background-image: url('../../img/video-bg.png');
  }

  &__video-icon {
    width: 38px;
    height: 27px;
    background-image: url('../../img/video-icon.png');
    background-repeat: no-repeat;
  }

  &__video-text {
    display: flex;
    flex-direction: column;
    width: 260px;
    padding: 15px;
  }

  &__video-caption {
    font-family: 'Pragmatica Book';
    color: #818285;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 10px;
  }

  &__video-description {
    text-decoration: none;
    font-family: 'Pragmatica Bold';
    color: #231f20;
    font-size: 16px;
    line-height: 19px;
  }

  &__description {
    display: flex;
    flex-direction: column;
    padding-top: 28px;
    margin-bottom: 5px;
    width: 485px;
  }

  &__description-caption {
    padding: 0;
    margin: 0;
    margin-bottom: 22px;
    font-family: 'Pragmatica Light';
    font-size: 26px;
    line-height: 31px;
    color: #221f1f;
  }

  &__description-ph {
    font-family: 'Pragmatica Book';
    font-size: 14px;
    line-height: 22px;
    color: #1f1f1f;
    margin: 0;
    margin-bottom: 25px;
  }

  &__description-author {
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 14px;
    color: #7f8085;
  }

  &__buttons {
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
  }

  &__button {
    width: 16px;
    height: 27px;
    background-color: transparent;
    background-size: contain;
    border: none;
  }

  &__button--back {
    background-image: url('../../img/btn-back.png');
    background-repeat: no-repeat;
  }

  &__button--forward {
    background-image: url('../../img/btn-forward.png');
    background-repeat: no-repeat;
    margin-left: 55px;
  }
}
