// В этом файле должны быть стили для БЭМ-блока slider-developments, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.slider-developments {

  $block-name:                &; // #{$block-name}__element

  width: 350px;
  padding-top: 40px;

  &__items {
    display: flex;
    flex-direction: column;
  }

  &__item {
    margin-bottom: 46px;
    padding-left: 74px;

    & a {
      text-decoration: none;
    }
  }

  &__item--name {
    padding-top: 15px;
    background-image: url('../img/slider-icon.png');
    background-repeat: no-repeat;
    background-position: 5px 0;
    min-height: 55px;
  }

  &__item-name {
    display: inline-block;
    font-family: 'Pragmatica Book';
    font-size: 13px;
    line-height: 18px;
    color: #1f1f1f;
  }

  &__item-position {
    display: inline-block;
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 14px;
    color: #7f8085;
  }

  &__item--vtb {
    position: relative;
    padding-right: 85px;
    margin-bottom: 35px;

    &::before {
      content: "!";
      position: absolute;
      display: block;
      top: -5px;
      left: 12px;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      border: 2px solid #f04239;
      text-align: center;
      color: #f04239;
      font-size: 22px;
    }
  }

  &__item-date {
    display: inline-block;
    font-family: 'Pragmatica Light';
    color: 231f20;
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 8px;
  }

  &__item-description {
    display: inline-block;
    font-family: 'Pragmatica Light';
    color: #ee2d24;
  }

  &__item--event {
    padding-right: 45px;
    margin-bottom: 39px;
    background-image: url(../img/document.png);
    background-repeat: no-repeat;
    background-position: 15px 0;
  }

  &__item-desc {
    display: inline-block;
    font-family: 'Pragmatica Book';
    font-size: 13px;
    line-height: 18px;
    color: #1f1f1f;
  }

  &__button-list {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    padding-left: 75px;
  }

  &__button-item {
    margin-right: 14px;
  }

  &__button {
    background-color: #e3e3e4;
    border: none;
    width: 13px;
    height: 13px;
    border-radius: 50%;
  }

  &__button--active {
    background-color: transparent;
    border: 1px solid #f8b239;
  }
}
