// В этом файле должны быть стили для БЭМ-блока user-block, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.user-block {

  $block-name:                &; // #{$block-name}__element
  display: flex;
  align-items: center;

  &__mail {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: 35px;
    width: 35px;
    height: 28px;
  }

  &__number {
    position: absolute;
    top: 0;
    right: 2px;
    width: 17px;
    height: 13px;
    background-color: #ee2d24;
    border-radius: 5px;
    text-align: center;
    font-family: 'Pragmatica Bold';
    font-size: 8px;
    line-height: 13px;
    color: #ffffff;
  }

  &__info {
    display: inline-block;
    width: 140px;
    height: 40px;
    padding-left: 49px;
    padding-top: 10px;
    text-decoration: none;
    color: #231f20;
    background-color: #fdda9d;
    background-image: url(../img/user-icon.png);
    background-repeat: no-repeat;
    background-position: 5px;
    border-radius: 25px;
    font-family: 'Pragmatica Light';
  }
}
