// В этом файле должны быть стили для БЭМ-блока portal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.portal {

  $block-name:                &; // #{$block-name}__element

  width: 285px;
  display: flex;
  flex-direction: column;

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-family: 'Pragmatica Bold';
    font-size: 14px;
    line-height: 12px;
    color: #221f1f;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 27px;
  }

  &__item {
    //
  }

  &__link {
    text-decoration: none;
    font-family: 'Pragmatica Light';
    font-size: 14px;
    line-height: 25px;
    color: #221f1f;
  }

  &__error {
    margin: 0;
    padding-right: 30px;
    font-family: 'Pragmatica Ligth';
    font-size: 12px;
    line-height: 19px;
    color: #808185;
  }
}
