// В этом файле должны быть стили для БЭМ-блока hot-line, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.hot-line {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  width: 285px;
  margin-right: 30px;

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 18px;
    font-family: 'Pragmatica Bold';
    font-size: 14px;
    line-height: 12px;
    color:#ee2d24;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 15px;
  }

  &__number {
    display: block;
    font-family: 'Pragmatica Bold';
    font-size: 12px;
    text-decoration: none;
    color: #211f1f;
  }

  &__number-desc {
    margin: 0;
    font-family: 'Pragmatica Book';
    font-size: 12px;
    line-height: 14px;
    color: #818285;
  }

  &__mail {
    font-family: 'Pragmatica Bold';
    font-size: 12px;
    line-height: 14px;
    text-decoration: none;
    color: #ee2d24;
    margin-bottom: 20px;
  }

  &__btn {
    display: inline-block;
    width: 215px;
    height: 40px;
    padding-top: 15px;
    background-color: #fdda9d;
    border-radius: 20px;
    text-decoration: none;
    text-align: center;
    font-family: 'Pragmatica Bold';
    font-size: 10px;
    color: #1f1f1f;
    text-transform: uppercase;
  }
}
