// В этом файле должны быть стили для БЭМ-блока news, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.news {

  $block-name:                &; // #{$block-name}__element

  width: 1230px;
  padding: 0 145px;
  border-bottom: 1px solid #e3e3e4;
  padding-bottom: 60px;
  margin-bottom: 55px;

  @media  (min-width: 1280px) {
    width: 100%;
  }

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 35px;
    list-style: none;

  }

  &__item {
    margin-right: 20px;

    &:nth-of-type(4) {
      margin-right: 0;
    }
  }

  &__item-link {
    position: relative;
    display: block;
    text-decoration: none;
  }

  &__item-img {
    display: block;
    max-width: 220px;
    height: 165px;
    margin-bottom: 23px;
  }

  &__item-img-desc {
    position: absolute;
    display: block;
    top: 115px;
    left: 10px;
    width: 60px;
    height: 60px;
    background-color: #fcd53b;
    padding-top: 32px;
    padding-left: 8px;
    color: #ffffff;
    font-family: 'Pragmatica Black';
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;

    &--red {
      background-color: #ee2d24;
    }

    &--yellow {
      background-color: #f57f29;
    }
  }

  &__item-desc {
    margin: 0;
    margin-bottom: 10px;
    padding: 0 12px;
    font-family: 'Pragmatica Book';
    color:#201f1f;
    font-size: 13px;
    line-height: 18px;

    &--text {
      padding-right: 30px;
    }
  }

  &__item-date {
    padding: 0 12px;
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 14px;
    color: #7f8085;
  }

  &__buttons {
    display: flex;
    justify-content: center;
  }

  &__button {
    width: 152px;
    height: 42px;
    padding-left: 48px;
    padding-top: 18px;
    background-color: #fdda9d;
    border-radius: 20px;
    text-decoration: none;
    font-family: 'Pragmatica Bold';
    font-size: 10px;
    line-height: 9px;
    color: #231f20;
    text-transform: uppercase;

    &--all {
      margin-right: 9px;
      background-image: url('../img/newspaper-icon.png');
      background-repeat: no-repeat;
      background-position: 10px 12px;
    }

    &--region {
      margin-left: 9px;
      padding-left: 46px;
      background-image: url('../img/pin-icon.png');
      background-repeat: no-repeat;
      background-position: 17px 12px;
    }
  }
}
