// В этом файле должны быть стили для БЭМ-блока quotes, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.quotes {

  $block-name:                &; // #{$block-name}__element
  width: 285px;
  margin-right: 30px;

  &__title {
    margin: 0;
    margin-bottom: 19px;
    font-family: 'Pragmatica Bold';
    font-size: 14px;
    line-height: 12px;
    color: #231f20;
  }

  &__date {
    display: inline-block;
    margin-bottom: 16px;
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 14px;
    color: #7f7f85;
  }

  &__block {
    width: 100%;
  }

  &__table {
    display: block;
    width: 100%;
    border-collapse: collapse;
    padding-top: 14px;
    padding-bottom: 10px;

    &:nth-child(1) {
      border-bottom: 1px solid #d7d7d9;
      border-top: 2px solid #d7d7d9;
    }

    &:nth-child(2) {
      border-bottom: 2px solid #d7d7d9;
    }
  }

  & tr {
    line-height: 10px;
    padding: 0;
  }

  & td {
    width: 57.5%;
    padding: 0;
  }

  & td > span {
    display: inline-block;
    height: 18px;
  }

  &__table-name {
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 12px;
    color: #211f1f;
  }

  &__table-value {
    margin-right: 5px;
    font-family: 'Pragmatica Bold';
    font-size: 12px;
    line-height: 12px;
    color: #211f1f;
  }

  &__table-value-desc {
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 12px;
    color: #211f1f;
  }

  &__table-motion {
    width: 100%;
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 12px;
    color: #46b900;
    background-image: url('../img/motion-up.png');
    background-repeat: no-repeat;
    background-position: 55% 2px;
  }

  &__table-open {
    margin-right: 6px;
    color: #7f8085;
    font-family: 'Pragmatica Light';
    font-size: 12px;
    line-height: 12px;
  }

  &__table-open-desc {
    font-family: 'Pragmatica Bold';
    font-size: 12px;
    line-height: 12px;
    color: #7f8085;
  }
}
